<template>
  <div class="left">
    <div class="partneradminHeader">
      <div>部门设置</div>
      <el-button @click.native="searchLeavePeo">查看离职员工</el-button>
    </div>

    <div class="tree">
      <el-tree
        :data="data"
        node-key="id"
        @node-click="handleNodeClick"
        default-expand-all
      >
        <span
          class="custom-tree-node"
          slot-scope="{ node, data }"
        >
          <svg-icon :icon-class="data.icons" />
          {{ node.label }}
          <span
            class="little_icon"
            @click="() => partRemove(data)"
          >
            <svg-icon :icon-class="data.oneicon" />
          </span>
          <span
            class="little_icon"
            @click="() => partAdd(data)"
          >
            <svg-icon :icon-class="data.twoicon" />
          </span>
          <span
            class="little_icon"
            @click="() => partEdit(data)"
          >
            <svg-icon :icon-class="data.threeicon" />
          </span>
        </span>
      </el-tree>
    </div>
    <!-- 修改部门名称 -->
    <div class="DIALOG">
      <el-dialog
        title="修改部门名称"
        :visible.sync="partEditVisible"
        width="30%"
        :before-close="handleClose"
      >
        <span>
          <span style="color:red;vertical-align:middle">*</span> 部门名称:
        </span>
        <el-input
          v-model="partEditInfo"
          style="width:300px"
        ></el-input>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="partEditVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="Submit_sure(partEditInfo)"
          >确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 添加新部门名称 -->
    <div class="DIALOG">
      <el-dialog
        title="添加子部门"
        :visible.sync="partAddVisible"
        width="30%"
        :before-close="handleClose"
      >
        <span>
          <span style="color:red;vertical-align:middle">*</span> 部门名称:
        </span>
        <el-input
          v-model="partAddInfo"
          style="width:300px"
        ></el-input>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="partAddVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="Submit_sure(partAddInfo)"
          >确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 添加新部门名称 -->
    <div class="DIALOG">
      <el-dialog
        title="删除部门"
        :visible.sync="partRemoveVisible"
        width="30%"
        :before-close="handleClose"
      >
        <span>确定要删除部门?</span>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="partRemoveVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="partRemoveVisible = false "
          >确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // DIALOG 初始化
      partEditVisible: false,
      partEditInfo: '',
      partAddVisible: false,
      partAddInfo: '',
      partRemoveVisible: false,
      data: [
        {
          id: 1000,
          label: '神盾局',
          icons: 'partneradmin_ku',
          oneicon: '30-heavydutytruck',
          twoicon: '29-truck',
          threeicon: '28-tank',
          children: [
            {
              id: 1001,
              label: '先进武器研发部',
              icons: 'partneradmin_mengb',
              oneicon: '30-heavydutytruck',
              twoicon: '29-truck',
              threeicon: '28-tank',
              children: [
                {
                  id: 10010,
                  label: '研发一部',
                  icons: 'partneradmin_muyou',
                  oneicon: '30-heavydutytruck',
                  twoicon: '29-truck',
                  threeicon: '28-tank',
                  children: []
                },
                {
                  id: 10011,
                  label: '研发二部',
                  icons: 'partneradmin_muyou',
                  oneicon: '30-heavydutytruck',
                  twoicon: '29-truck',
                  threeicon: '28-tank',
                  children: []
                },
                {
                  id: 10012,
                  label: '研发三部',
                  icons: 'partneradmin_muyou',
                  oneicon: '30-heavydutytruck',
                  twoicon: '29-truck',
                  threeicon: '28-tank',
                  children: []
                }
              ]
            },
            {
              id: 1002,
              label: '战争伦理委员会',
              icons: 'partneradmin_mengb',
              oneicon: '30-heavydutytruck',
              twoicon: '29-truck',
              threeicon: '28-tank',
              children: [
                {
                  id: 10020,
                  label: '委员会二部',
                  icons: 'partneradmin_muyou',
                  oneicon: '30-heavydutytruck',
                  twoicon: '29-truck',
                  threeicon: '28-tank',
                  children: []
                }
              ]
            }
          ]
        }
      ]
    }
  },
  methods: {
    // el-tree 点击事件
    handleNodeClick(data, node, value) {
      this.$store.dispatch('partManage/searchPart', data)
      // .then((response)=>{
      //   console.log(response)
      // })
     },
    // DIALOG方法集合
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    partEdit(e) {
      this.partEditVisible = true
    },
    // button 按钮
    Submit_sure(e) {
      console.log(e)
      this.partEditVisible = false
      this.partAddVisible = false
      this.partEditInfo = ''
      this.partAddInfo = ''
    },
    partAdd(e) {
      this.partAddVisible = true
    },
    partRemove(e) {
      this.partRemoveVisible = true
    },
    searchLeavePeo(e) {
      console.log('点击')
    }
  }
}
</script>

<style lang="scss" scoped>
.left {
  margin-left: 20px;
  font-size: 14px;
  color: #666;
  margin-top: 20px;
  .partneradminHeader {
    height: 40px;
    display: flex;
    border-bottom: 1px solid #ccc;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
  }
}
.custom-tree-node {
  width: 100%;
  .little_icon {
    float: right;
    margin-left: 10px;
  }
}
</style>