<template>
  <div class="right">
    <el-tabs
      v-model="activeName"
      @tab-click="handleClick"
    >
      <el-tab-pane
        label="人员设置"
        name="first"
      >
        <el-row>
          <el-col
            :span="12"
            class="COlFix"
          >
            <span style="margin-right:10px">关键字查询:</span>
            <el-input
              placeholder="请输入姓名或者电话"
              v-model="TelOrName_peo"
              style="width:250px;"
            ></el-input>
          </el-col>
          <el-col :span="3">
            <el-button type="primary">查询</el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="primary">添加人员</el-button>
          </el-col>
        </el-row>

        <div class="table_peo">
          <el-table style="margin-top:20px;">
            <!-- table 的表格头 -->
            <template v-for="(item,index) in Columns_peo">
              <compon-table
                :key="index"
                :item="item"
                v-bind="$attrs"
              />
            </template>
            <el-table-column
              fixed="right"
              width="240px"
              label="操作"
            >
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index , scope.row)"
                >添加岗位</el-button>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index , scope.row)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                >离职</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        label="岗位设置"
        name="second"
      >
        <el-row>
          <el-col
            :span="12"
            class="COlFix"
          >
            <span style="margin-right:10px">关键字查询:</span>
            <el-input v-model="TelOrName_gang" placeholder="请输入姓名或者电话" style="width:250px;" />
          </el-col>
          <el-col :span="3">
            <el-button type="primary">查询</el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="primary">添加岗位</el-button>
          </el-col>
        </el-row>

        <div class="table_gang">
          <el-table style="margin-top:20px;">
            <!-- table 的表格头 -->
            <template v-for="(item,index) in Columns_gang">
              <compon-table
                :key="index"
                :item="item"
                v-bind="$attrs"
              />
            </template>
            <el-table-column
              fixed="right"
              width="240px"
              label="操作"
            >
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index , scope.row)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                >删除</el-button>

                <el-button
                  size="mini"
                  type="danger"
                  @click="handleHouse(scope.$index, scope.row)"
                >楼盘权限</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import ComponTable from '@/component/BaseTable/TableHeader'
import store from '@/store'
export default {
  components: { ComponTable },
  data() {
    return {
      TelOrName_peo: '',
      TelOrName_gang: '',
      activeName: 'first',
      Columns_peo: [
        {
          label: '姓名',
          prop: 'name'
        },
        {
          label: '岗位部门',
          prop: 'part'
        },
        {
          label: '手机号码',
          prop: 'tel'
        }
      ],
      Columns_gang: [
        {
          label: '岗位名称',
          prop: 'gangName'
        }
      ]
    }
  },
  watch() {
    // console.log(store)
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    }
  }
}
</script>

<style lang="scss" scoped>
.right {
  margin-left: 20px;
  margin-top: 20px;
}
.COlFix {
  display: flex;
  align-items: center;
  font-size: 14px;
}
</style>
